(function() {
  angular.module('homemademessClient', ['ngCookies', 'ngSanitize', 'ngAria', 'ngResource', 'ui.router', 'akoenig.deckgrid', 'ngMaterial', 'ngDropzone', 'ngTagsInput', 'ngLodash']).filter('taglink', function() {
    return function(input) {
      return input.replace(/\s/g, '_');
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('TaggedCtrl', [
    '$scope', '$stateParams', 'slides', function($scope, $stateParams, slides) {
      $scope.$parent.view = slides.data;
      return $scope.tag = $stateParams.tag.replace(/_/g, ' ');
    }
  ]);

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('slideshow', function() {
    var directive, slideshowCtrl;
    slideshowCtrl = [
      '$scope', '$mdDialog', '$stateParams', '$state', '$document', function($scope, $mdDialog, $stateParams, $state, $document) {
        $scope.open = false;
        $scope.transition = false;
        $document.bind('keyup', function(e) {
          var slide;
          if (($state.params.slide != null) && $scope.open && !$scope.transition) {
            slide = Number($state.params.slide);
            switch (e.which) {
              case 39:
                if ($scope.view.images.length > (slide + 1)) {
                  return $state.go($state.current, {
                    slide: slide + 1
                  });
                }
                break;
              case 37:
                if ((slide - 1) > -1) {
                  return $state.go($state.current, {
                    slide: slide - 1
                  });
                }
                break;
              case 38:
                return $state.go($state.current, {
                  slide: 0
                });
              case 40:
                return $state.go($state.current, {
                  slide: $scope.view.images.length - 1
                });
            }
          }
        });
        $scope.$on('slidesLayout', function(event) {
          if ($stateParams.slide) {
            return $scope.updateShowState($stateParams.slide);
          }
        });
        $scope.$on('$stateChangeStart', function(e, toState, toParams, fromState, fromParams) {
          if (!$scope.open && (toParams.slide != null)) {
            $scope.updateShowState(toParams.slide);
          }
          if (!toParams.slide && $scope.open) {
            return $mdDialog.hide();
          }
        });
        $scope.updateShowState = function(i) {
          if (!$scope.open && $scope.view.images[i]) {
            $scope.open = true;
            return $scope.showSlide(i);
          }
        };
        return $scope.showSlide = function(i) {
          i = Number(i);
          return $mdDialog.show({
            clickOutsideToClose: true,
            locals: {
              i: i,
              img: $scope.view.images[i].derivative[2].uri,
              label: $scope.view.images[i].filename
            },
            scope: $scope,
            preserveScope: true,
            onRemoving: function() {
              return $scope.open = false;
            },
            template: '<md-dialog aria-label="{{label}}" class="slide" ng-class="{loading: transition}">' + '  <md-dialog-content>' + '     <img ng-src="{{img}}" class="img-responsive" ng-class="imgclasses" />' + '     <div class="loading-spinner"></div>' + '  </md-dialog-content>' + '</md-dialog>',
            controller: function($scope, $mdDialog, $stateParams, i, img, label) {
              $scope.i = i;
              $scope.img = img;
              $scope.label = label;
              $scope.imgclasses = {};
              $scope.$on('$stateChangeStart', function(e, toState, toParams, fromState, fromParams) {
                if (toParams.slide != null) {
                  return $scope.update(toParams.slide);
                }
              });
              $scope.update = function(i) {
                var loader, src;
                $scope.transition = true;
                src = $scope.view.images[i].derivative[2].uri;
                loader = new Image();
                loader.onload = function() {
                  $scope.imgclasses = $scope.toggleClass(Number(i));
                  $scope.i = Number(i);
                  $scope.img = src;
                  $scope.label = $scope.view.images[i].filename;
                  $scope.transition = false;
                  return $scope.$digest();
                };
                return loader.src = src;
              };
              return $scope.toggleClass = function(i) {
                var classes;
                classes = {
                  up: false,
                  down: false,
                  left: false,
                  leftagain: false,
                  right: false,
                  rightagain: false
                };
                switch (false) {
                  case !(i < $scope.i && (($scope.i - i) > 1) && i === 0):
                    classes.up = true;
                    break;
                  case !(i > $scope.i && ((i - $scope.i) > 1) && i === ($scope.view.images.length - 1)):
                    classes.down = true;
                    break;
                  case !(i < $scope.i && ($scope.i - i) === 1):
                    if ($scope.imgclasses.left) {
                      classes.leftagain = true;
                    } else {
                      classes.left = true;
                    }
                    break;
                  case !(i > $scope.i && ((i - $scope.i) === 1)):
                    if ($scope.imgclasses.right) {
                      classes.rightagain = true;
                    } else {
                      classes.right = true;
                    }
                }
                return classes;
              };
            }
          }).then(null, function() {
            return $state.go($state.$current.parent);
          });
        };
      }
    ];
    return directive = {
      restrict: 'E',
      controller: slideshowCtrl
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('openSlide', function() {
    return {
      restrict: 'A',
      link: function(scope, element, attrs) {
        return element.bind("click", function($state, $timeout) {
          return function(e) {
            var transition;
            transition = $timeout(function() {
              $state.go(attrs.state, attrs.params);
            });
            e.preventDefault();
            e.preventDefault = function() {};
            if (ignorePreventDefaultCount-- <= 0) {
              $timeout.cancel(transition);
            }
          };
        });
      }
    };
  }).directive('slides', function() {
    var directive, slidesCtrl;
    slidesCtrl = [
      '$scope', '$state', '$rootScope', function($scope, $state, $rootScope) {
        $scope.curSt = $state.current.name + '.slide';
        $scope.getParams = function(n) {
          return {
            slide: n
          };
        };
        $scope.getHref = function(i) {
          return $state.href('.slide', {
            slide: i
          });
        };
        $scope.broadcastLayout = function() {
          return $rootScope.$broadcast('slidesLayout');
        };
        return $scope.aspect = function(img, n) {
          var a, aspect, columns, rows;
          a = Math.round((img.height / img.width) * 100) / 100;
          if (a < 1) {
            columns = (function() {
              switch (false) {
                case !(a < 0.6):
                  return 8;
                case !(a < 0.4):
                  return 6;
                case !(a < 0.25):
                  return 4;
                default:
                  return 12;
              }
            })();
            rows = Math.round(columns * a);
          } else {
            rows = (function() {
              switch (false) {
                case !(a > 1.34):
                  return 16;
                case !(a > 2):
                  return 24;
                case !(a > 3):
                  return 36;
                default:
                  return 12;
              }
            })();
            columns = (Math.round(Math.round(rows * img.width / img.height) / 2)) * 2;
          }
          aspect = [columns, rows];
          return aspect[n];
        };
      }
    ];
    return directive = {
      restrict: 'E',
      controller: slidesCtrl,
      templateUrl: 'app/components/slides/slides.html'
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').directive('searchbar', function() {
    var SearchCtrl, directive;
    SearchCtrl = [
      '$scope', '$resource', 'lodash', 'apiUrl', function($scope, $resource, lodash, apiUrl) {
        var Auto, Images;
        $scope.mapTags = function(tags) {
          return lodash.uniq(lodash.map(tags, '_id'));
        };
        Images = $resource(apiUrl + '/images');
        $scope.redoSearch = function() {
          return Images.get({
            tags: $scope.mapTags($scope.tags)
          }, function(result) {
            return $scope.view.images = result.images;
          });
        };
        Auto = $resource(apiUrl + '/auto');
        return $scope.findTags = function(value) {
          return Auto.query({
            q: value
          }).$promise;
        };
      }
    ];
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/searchbar/searchbar.html',
      controller: SearchCtrl
    };
  });

}).call(this);


(function() {
  'use strict';
  angular.module('homemademessClient').controller('MainCtrl', [
    '$scope', '$state', 'slides', function($scope, $state, slides) {
      $scope.view = slides.data;
      $scope.tags = [];
      return $scope.$on('$viewContentLoaded', function(event) {
        return $scope.$broadcast('viewInit', $state.params);
      });
    }
  ]);

}).call(this);


(function() {
  angular.module('homemademessClient').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainCtrl',
      resolve: {
        slides: function($http, apiUrl) {
          return $http.get(apiUrl + '/images');
        }
      }
    }).state('home.slide', {
      url: 'slide/:slide'
    }).state('home.tagged', {
      url: 'tagged/:tag/',
      templateUrl: 'app/main/tagged/tagged.html',
      controller: 'TaggedCtrl',
      resolve: {
        slides: function($http, apiUrl, $stateParams) {
          return $http.get(apiUrl + '/tagged/' + $stateParams.tag);
        }
      }
    }).state('home.tagged.slide', {
      url: 'slide/:slide'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);


(function() {
  angular.module('homemademessClient').controller('IndexCtrl', [
    '$scope', '$window', function($scope, $window) {
      var detectSmall;
      $scope.bodyClass = {};
      detectSmall = function() {
        if ($window.innerWidth < 600) {
          return true;
        }
        return false;
      };
      $scope.bodyClass.small = detectSmall();
      return angular.element($window).bind('resize', function() {
        return $scope.bodyClass.small = detectSmall();
      });
    }
  ]);

}).call(this);


(function() {
  angular.module('homemademessClient').constant('apiUrl', 'http://api.homemademess.com');

}).call(this);


